<template>
    <div>
        <el-card class="box-card common_box bot">
            <h3 class="title"><el-divider content-position="left">添加IBeacon设备</el-divider></h3>
            <el-form :model="addForm" label-width="200px" label-position="top">
                <el-form-item label="IBeacon设备组名称" prop="group_name">
                    <el-select v-model="addForm.group_id" placeholder="请选择IBeacon设备组">
                        <el-option v-for="item in groupList" 
                            :key="item.group_id" 
                            :label="item.group_name" 
                            :value="item.group_id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-divider></el-divider>
                <div><el-button type="success" @click="addItem">追加设备信息</el-button></div>
                <br>
                <div class="item_box" v-for="(item, i) in addForm.device_identifiers">
                    <h5><el-divider content-position="left">第<span>{{i+1}}</span>个设备</el-divider></h5>
                    <div class="inner">
                        <!-- <div class="ibox"><label>设备ID</label><el-input v-model="item.device_id"></el-input></div> -->
                        <div class="ibox"><label>UUID</label><el-input v-model="item.uuid"></el-input></div>
                        <div class="ibox"><label>主版本</label><el-input v-model="item.major"></el-input></div>
                        <div class="ibox"><label>次版本</label><el-input v-model="item.minor"></el-input></div>
                    </div>  
                </div>
            </el-form>
        </el-card>
        <el-button type="primary" @click="doAdd">定添加</el-button> 
    </div>
</template>

<script>
    import axios from "@/util.js";
    export default {
        data() {
            return {
                list:[],
                groupList:[],
                addForm: {
                    group_id: "",
                    device_identifiers: [
                        {
                            "device_id":"",  
                            "uuid":"",    
                            "major":"",
                            "minor":""
                        }
                    ]
                }
            }
        },
        inject:["reload"],
        mounted() {
            this.queryGroups();
        },
        methods: {
            queryGroups() {
                axios.post('/api/getBeaconsGroup', {
                    begin: 0,
                    count: 10
                }).then((res) => {
                    if (!res.code) {
                        let data = JSON.parse(res.data);
                        if (!data.errcode) {
                            // console.log(data.data.groups)
                            this.groupList = data.data.groups;
                        }                
                    }
                });
            },
            addItem() {
                this.addForm.device_identifiers.push({
                    "device_id":"",  
                    "uuid":"",    
                    "major":"",
                    "minor":""
                });
            },
            doAdd() {
                if (!this.addForm.group_id) {
                    this.$message.error("请选择IBeacon设备组")
                    return;
                }
                let status = true;
                // this.addForm.group_id = this.addForm.group_id + '';
                this.addForm.device_identifiers.forEach((item) => {
                    for (let key in item) {
                        if (!item[key] && key != "device_id") {
                            status = false;
                        }
                        // if (key == "device_id") {
                        //     item[key] = parseInt(item[key]);
                        // }
                        // if (key == "major") {
                        //     item[key] = parseInt(item[key]);
                        // }
                        // if (key == "minor") {
                        //     item[key] = parseInt(item[key]);
                        // }
                        
                    }
                });

                if (!status) {
                    this.$message.error("每个设备字段都是必填");
                    return;
                }
                axios.post("/api/addH5Beacon", this.addForm).then((res) => {
                    if (!res.code) {
                        let data = JSON.parse(res.data);
                        if (!data.errcode) {
                            this.$message.success(data.errmsg);
                        } else {
                            this.$message.error("错误码：" + data.errcode + "，错误信息："+ data.errmsg);
                        }               
                    }
                });

            }
        }

    }
</script>

