    border: 1px solid #f4f4f4;
    padding: 15px;
    width:100%;
    box-sizing: border-box;
    box-shadow: 0 2px 2px rgba(0,0,0,.1);
    
    margin-bottom: 15px;
    h5 {
        span{
            font-size: 18px;
            color: orange;
            margin: 0 5px;
        }
    }
    .inner{
        display: flex;
    }
    .ibox{
        width: 25%;
        padding: 0 10px;
        &:first-child{
            width: 35%;
        }
    }
    label{
        line-height: 32px;
    }
}
</style>