<template>
    <div>
        <el-card class="box-card common_box bot">
            <h3 class="title">H5的IBeacon的相关操作</h3>
            <div>
                <el-button @click="add" type="primary">添加IBeacon设备组</el-button>
            </div>
            <br>
            <el-table :data="groupList" border  
                @expand-change="expandChange"           
                style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <table class="inner_table">
                            <thead>
                                <tr>
                                    <th>设备UUID</th>
                                    <th>设备major</th>
                                    <th>设备minor</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in devices" :key="index">
                                    <td>{{ item.uuid }}</td>
                                    <td>{{ item.major }}</td>
                                    <td>{{ item.minor }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="group_id"
                    label="设备组ID"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="group_name"
                    label="设备名字"
                    width="180">
                </el-table-column>
                <el-table-column label="操作" fixed="right">
                    <template slot-scope="scope">
                        <el-button type="text" @click="editGroup(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="deleteGroup(scope.row)" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            :title="operateTitle"
            :visible.sync="dialogVisible"
            width="50%"
            :before-close="handleClose">
            <el-form :model="addForm" label-width="200px">
                <input type="hidden" v-model="addForm.group_id">
                <el-form-item label="IBeacon设备组名称" prop="group_name">
                    <el-input v-model="addForm.group_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="doAdd">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import axios from "@/util.js";
    export default {
        data() {
            return {
                dialogVisible: false,
                operateTitle: "添加IBeacon设备组",
                addForm: {
                    group_id: "",
                    group_name: ""
                },
                groupList:[],
                devices: [],
                editRow: null
            }
        },
        inject:["reload"],
        mounted() {
            this.query();
        },
        methods: {
            expandChange(row, expanded) {
                if (!expanded.length) return;
                axios.post("/api/getBeaconsByGroupId", {
                    "group_id":row.group_id,
                    "begin":0,
                    "count":100
                }).then((res) => {
                    if (!res.code) {
                        let data = JSON.parse(res.data);
                        if (!data.errcode) {
                            if (!data.data.total_count) {
                                this.$message.warning("目前还没有绑定的设备");
                            }
                            this.devices = data.data.devices;
                        }                
                    }
                })
            },
            query() {
                axios.post('/api/getBeaconsGroup', {
                    begin: 0,
                    count: 10
                }).then((res) => {
                    if (!res.code) {
                        let data = JSON.parse(res.data);
                        if (!data.errcode) {
                            // console.log(data.data.groups)
                            data.data.groups.forEach((item) => {
                                item.hasChildren = true;
                            });

                            this.groupList = data.data.groups;
                        }                
                    }
                });
            },
            add() {
                this.dialogVisible = true;
            },
            handleClose() {
                this.dialogVisible = false;
                this.addForm = {
                    group_name: "",
                    group_id: ""
                };
                this.editRow = null;
            },
            editGroup(row) {
                this.editRow = row;
                this.addForm.group_name = row.group_name;
                this.addForm.group_id = row.group_id;
                this.dialogVisible = true;
            },
            deleteGroup(row) {
                this.$confirm('此操作将永久删除该设备组, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios.post("/api/deleteBeaconsGroup", {
                        "group_id":row.group_id
                    }).then((res) => {
                        if (!res.code) {
                            let data =  JSON.parse(res.data);
                            if (!data.errcode) {
                                this.$message.success(data.errmsg);
                                this.reload();
                            }                
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            },
            doAdd() {
                if (!this.addForm.group_name) {
                    this.$message.error("填写IBeacon设备组名称")
                    return;
                }
                if (this.editRow) {
                    axios.post("/api/editBeaconsGroup", {
                        group_name: this.addForm.group_name,
                        group_id: this.addForm.group_id
                    }).then((res) => {
                        if (!res.code) {
                            let data =  JSON.parse(res.data);
                            if (!data.errcode) {
                                this.$message.success(data.errmsg);
                                this.reload();
                                this.handleClose();
                            }                
                        }
                    });
                } else {
                    axios.post("/api/setBeaconsGroup", {
                        group_name: this.addForm.group_name
                    }).then((res) => {
                        if (!res.code) {
                            let data =  JSON.parse(res.data);
                            if (!data.errcode) {
                                this.$message.success(data.errmsg);
                                this.reload();
                                this.handleClose();
                            }
                        }
                    });
                }
                
            }
        }
    }
</script>

