<template>
    <el-card class="box-card common_box">
        <h3 class="title">修改微信菜单</h3>
        <el-form ref="form" :model="form" label-position="top">
            <el-form-item label="微信菜单代码">
                <el-input type="textarea" rows="20" v-model="form.info"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit"><svg-icon icon-class="py"/> 立即创建或修改</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>
import axios, { getFormatData } from "@/util.js";

export default {
    data() {
        return {
            form: {
                info: ""
            }
        };
    },
    inject: ['reload'],
    mounted() {
        this.getMenus();
    },
    methods: {
        getMenus() {
            axios.get("/api/getMenus").then((res) => {
                if (!res.code) {
                    let data = JSON.parse(res.data);
                    data.selfmenu_info.button = data.selfmenu_info.button.map((item) => {
                        if (item.sub_button) {
                            item.sub_button = item.sub_button.list;
                        }
                        return item;
                    });
                    this.form.info = getFormatData(JSON.stringify(data.selfmenu_info));
                }
            });
        },
        onSubmit() {
            var url = "/api/setMenus";
            var that = this;
            var menusString = JSON.stringify(JSON.parse(this.form.info));
            axios.post(url, {
                data: menusString
            }).then((data)=> {
                if (data.errcode == 0) {
                    this.reload();
                }
            });
        }
    }
};
</script>

<style lang="less">
.common_box {
    .title {
        margin-bottom: 15px;
    }
}
</style>