<template>
    <el-main>
        <el-card class="box-card common_box bot">
            <h3 class="title">摇一摇申请:审核状态</h3>
            <div>
                <div><strong>申请时间：</strong><span>{{ shakeStatus.apply_time }}</span></div>
                <div><strong>申请状态：</strong>
                    <span v-if="shakeStatus.audit_status == 0">审核未通过</span>
                    <span v-else-if="shakeStatus.audit_status == 1">审核中</span>
                    <span v-else-if="shakeStatus.audit_status == 2">审核已通过</span>
                </div>
                <div><strong>申请反馈：</strong><span>{{ shakeStatus.audit_comment }}</span></div>
            </div>
        </el-card>
        <el-card class="box-card common_box bot">
            <h3 class="title">设备申请状态</h3>
            <div>
                <div style="margin-top: 15px;">
                    <el-input placeholder="请输入设备申请批次ID" v-model="device_apply_id">
                        <el-button @click="queryApplyShakeDeviceStatus" slot="append">开始查询</el-button>
                    </el-input>
                </div>
            </div>
        </el-card>
    </el-main>
</template>

<script>
import axios from "@/util.js";

export default {
    data() {
        return {
            shakeStatus: {
                audit_comment:"",
                apply_time: "",
                audit_status: 3,
                audit_time:""
            },
            device_apply_id: null
        };
    },
    mounted() {
        this.queryApplyShakeStatus();
    },
    methods: {
        // 查看摇一摇周边申请状态
        queryApplyShakeStatus() {
            axios.get("/api/getShakeAround").then(res => {
                if (!res.code) {
                   let data = JSON.parse(res.data);
                   if (!data.errcode) {
                        this.shakeStatus = data.data
                        this.shakeStatus.apply_time = new Date(this.shakeStatus.apply_time * 1000).format("yyyy-MM-dd");
                   }
                   
                } else {

                }
            });
        },

        queryApplyShakeDeviceStatus() {
            if (!this.device_apply_id) {
                this.$message.error("请填写查询额批次ID");
                return;
            }
            axios.post("/api/getShakearoundDevices", {
                "apply_id": this.device_apply_id
            }).then(res => {
                console.log(res);
                if (!res.code) {
                   let data = JSON.parse(res.data);
                   if (!data.errcode) {
                        
                   } else {
                        this.$message.error(data.errmsg);
                   }
                   
                } else {
                     this.$message.error(res.err);
                }
            });
        }
    }
};
</script>



