<template>
    <el-container>
        <el-header class="header">
            <div class="header_wrap">
                <h3 class="title">
                    <strong><svg-icon icon-class="wechat" /> 微信后台系统</strong>
                    <span>WeChat Management System</span>
                </h3>
                <div class="login_info">
                    <i class="el-icon-user-solid"></i>
                    <b>&nbsp;&nbsp;{{ user }}&nbsp;&nbsp;&nbsp;</b>
                    <el-button @click="logout" type="primary">退出登录</el-button>
                </div>
            </div>
        </el-header>
        <div class="nav">
            <el-menu :default-active="activeIndex" mode="horizontal" :router="true">
                <el-menu-item index="/dashboard"><i class="el-icon-s-home" />仪表盘</el-menu-item>
                <el-submenu index="2">
                    <template slot="title"><i class="el-icon-menu" />我的工作台</template>
                    <el-menu-item index="/menus">修改微信菜单</el-menu-item>
                    <el-menu-item index="/weixinFlowers">关注者列表</el-menu-item>
                </el-submenu>
                <el-submenu index="3">
                    <template slot="title"><i class="el-icon-map-location" />H5相关IBeacon服务</template>
                    <el-menu-item index="/h5Beacon/beaconGroupOperate">IBeacon组列表</el-menu-item>
                    <el-menu-item index="/h5Beacon/addBeaconDevice">添加IBeacon设备</el-menu-item>
                </el-submenu>
                <el-submenu index="4">
                    <template slot="title"><i class="el-icon-magic-stick" />摇一摇服务</template>
                    <el-menu-item index="/shake/dashboard">查看申请摇一摇的服务</el-menu-item>
                    <el-menu-item index="/shake/device">摇一摇中的iBeacon设备申请</el-menu-item>
                </el-submenu>
            </el-menu>
        </div>
        <el-main class="main">
            <el-row :gutter="20">
                <el-col :span="24">
                    <router-view v-if="isRouterAlive"></router-view>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
import { cookie } from "@/util.js";

export default {
    data() {
        return {
            activeStep: 1,
            activeIndex: "dashboard",
            dialogVisible: false,
            user: "",
            isRouterAlive: true
        };
    },
    provide (){
        return {
            reload: this.reload
        }
    },
    created() {
        this.activeIndex = this.$route.path;
        this.user = cookie.get('user');
    },
    components: {},
    methods: {
        reload() {
            this.isRouterAlive = false;
            this.$nextTick(function() {
                this.isRouterAlive = true;
            });
        },
        logout() {
            cookie.del('token');
            cookie.del('user');
            this.$router.push({
                path: "/login"
            });
        }
    }
};
</script>

