import Vue from 'vue';
import VueRouter from "vue-router";
import Page404 from "./404.vue";

import Login from "./containers/login.vue";
import Home from "./containers/home.vue";
import Dashboard from "./containers/childs/dashboard.vue";
import Menus from "./containers/childs/weixinMenus.vue";
import weixinFlowers from "./containers/childs/weixinFlowers.vue";
import locationDashboard from "./containers/location/dashboard.vue";
import weixinDevice from "./containers/location/weixinDevice.vue";
import addBeaconDevice from "./containers/h5Beacon/addBeaconDevice.vue";
import beaconGroupOperate from "./containers/h5Beacon/beaconGroupOperate.vue";


Vue.use(VueRouter);


const routes = [{
        path: '/',
        component: Home,
        redirect: '/dashboard',
        children: [{
            path: "dashboard",
            component: Dashboard,
        }, {
            path: "menus",
            component: Menus,
        },{
            path: "weixinFlowers",
            component: weixinFlowers,
        }],
    },
    {
        path: '/h5Beacon',
        component: Home,
        redirect: '/h5Beacon/beaconGroupOperate',
        children: [{
            path: "addBeaconDevice",
            component: addBeaconDevice,
        }, {
            path: "beaconGroupOperate",
            component: beaconGroupOperate,
        }],
    },
    {
        path: '/shake',
        component: Home,
        redirect: '/shake/dashboard',
        children: [{
            path: "dashboard",
            component: locationDashboard,
        }, {
            path: "device",
            component: weixinDevice,
        }],
    },
    {
        path: '/login',
        component: Login
    },
    {
        path: '*',
        component: Page404,
    }
]


const router = new VueRouter({
    routes // (缩写) 相当于 routes: routes
});

export default router;