<template>
    <div>
        <el-card class="box-card common_box">
            <h3 class="title">关注者列表</h3>
            <el-alert type="success" effect="dark">共有{{ total }}个关注者</el-alert>
            <br />
            <el-table size="small" :data="tableData" border max-height="500" style="width: 100%;">
                <el-table-column prop="openid" label="用户的openId"></el-table-column>
                <el-table-column label="操作信息">
                    <template slot-scope="scope">
                        <el-button  type="primary" size="mini" @click="queryUser(scope.row)">查看用户信息</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <el-drawer title="用户信息" :visible.sync="visible" :with-header="false">
            <el-card class="box-card" v-loading="loading">
                <div class="user_name bot">
                    <el-image style :src="user.headimgurl" fit="contain"></el-image>
                    <strong>{{user.nickname}}</strong>
                </div>
                <div class="bot">
                    <strong>用户地址：</strong>
                    <span>{{user.country}} - {{user.province}} - {{user.city}}</span>
                </div>
            </el-card>
        </el-drawer>
    </div>
</template>

<script>
import axios from "@/util.js";

export default {
    data() {
        return {
            tableData: [],
            total: 0,
            next_openid: "",
            visible: false,
            loading: false,
            user: {}
        };
    },
    inject: ["reload"],
    mounted() {
        var url = "/api/getFlowers";
        var that = this;
        axios.get(url).then(res => {
            if (!res.code) {
                let data = JSON.parse(res.data);
                this.total = data.total;
                this.tableData = data.data.openid;
                this.next_openid = data.next_openid;
                this.tableData = this.tableData.map(item => {
                    return { openid: item };
                });
            }
        });
    },
    methods: {
        queryUser(row) {
            this.visible = true;
            this.loading = true;
            var url = `/api/getUserInfo/${row.openid}`;
            var that = this;
            axios.get(url).then(res => {
                if (!res.code) {
                    let data = JSON.parse(res.data);
                    this.user = data;
                    this.loading = false;
                }
            });
        }
    }
};
</script>

<style lang="less" scoped>
.common_box {
    .title {
        margin-bottom: 15pxs;
    }
}
.bot{
    margin-bottom: 15px;
}
.user_name {
    font-size: 18px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    vertical-align: middle;
    .el-image {
        width: 60px;
        height: 60px;
        border: 1px solid #eee;
        border-radius: 5px;
        padding: 5px;
        margin-right: 15px;
    }
}
</style>